import React from 'react';
import CardContainer from './CardContainer';
import axios from 'axios';

const formStyle = {
    width: '50%',
    margin: 'auto'
}

const inputStyle = {
    width: '20%',
    display: 'block',
    padding: '10px',
    textAlign: 'center',
    border: '2px solid black',
    borderRadius: '20px',
    margin: '30px auto 0 auto',
}

const rankOrder = ['Cost','Proximity to campus','Proximity to each other','Safety','Room space','Overall living space'];

class PreferenceForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            ranking: ['Cost','Proximity to campus','Proximity to each other','Safety','Room space','Overall living space'],
        };
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.reorderRanking = this.reorderRanking.bind(this);
    }

    handleInputChange(event) {
        this.setState({name: event.target.value});
    }

    handleSubmit(event) {
        event.preventDefault();
        let name = this.state.name.trim();
        let ranking = this.state.ranking;
        let scores = [];
        for (let i = 0; i < rankOrder.length; i++) {
            const index = ranking.indexOf(rankOrder[i]);
            const score = ranking.length - index;
            scores.push(score);
        }
        if(!name) {
            alert('Please enter a name');
            return;
        }
        else {
            axios.post('/api/submissions', {
                name: name,
                scores: {
                    cost: scores[0],
                    proximityCampus: scores[1],
                    proximityOther: scores[2],
                    safety: scores[3],
                    roomSpace: scores[4],
                    livingSpace: scores[5]
                }
            })
            .then(function (response) {
                console.log(response);
                alert('Success!');
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    }

    reorderRanking(newRanking) {
        this.setState({ranking: newRanking});
    }

    render() {
        return (
            <form style = {formStyle} onSubmit = {this.handleSubmit}>
                <input placeholder = 'Name' style = {inputStyle} type = 'text' value = {this.state.name} onChange = {this.handleInputChange}/>
                <CardContainer cardMessages = {this.state.ranking} reorderRanking = {this.reorderRanking}/>
                <input style = {inputStyle} type = 'submit' value = 'Submit'/>
            </form>
        )
    }
}

export default PreferenceForm;





