import React from 'react';
import Card from './Card';
import { Droppable, DragDropContext} from 'react-beautiful-dnd';

const reorder = (list, startIndex, endIndex) => {
    const [item] = list.splice(startIndex, 1);
    list.splice(endIndex, 0, item);
    return list;
};

const listStyle = {
    margin: 'auto',
    marginTop: '30px'
};

class CardContainer extends React.Component {
    constructor(props) {
        super(props);
        const {cardMessages, reorderRanking} = props;
        this.state = {
            cardMessages: cardMessages,
            reorderRanking: reorderRanking,
        };
        this.onDragEnd = this.onDragEnd.bind(this);
    }

    onDragEnd(result) {
        if (!result.destination) {
            return;
        }
        const newList = reorder(this.state.cardMessages, result.source.index, result.destination.index);
        this.setState({
            cardMessages: newList
        });
        this.state.reorderRanking(newList);
    }

    render() {
        const cards = [];
        for (let i = 1; i <= this.state.cardMessages.length; i++) {
            cards.push(<Card key = {i} message = {i.toString() + '. ' + this.state.cardMessages[i-1]} index = {i}/>);
        }
        return (
            <DragDropContext onDragEnd = {this.onDragEnd}>
                <Droppable droppableId = 'droppable'>
                    {(provided, snapshot) => (
                        <div style = {listStyle} ref = {provided.innerRef}>
                            {cards}
                            {provided.placeholder}
                        </div>
                    )}
                </Droppable>
            </DragDropContext>
        );
    }
}

export default CardContainer;



